@import url(https://fonts.googleapis.com/css2?family=Muli&display=swap);
@media only screen and (max-width: 992px) {
  #header .animated{
    width: 90%!important;
  }
} 
@media only screen and (max-width: 766px) {
  #header .animated{
    width: 50%!important;
  }
}
@media only screen and (max-width: 600px) {
  #header .animated{
    width: 75%!important;
  }
} 
@media only screen and (min-width: 768px) {
  footer form .d-flex{
    margin-right: 80px;
  }
} 




html{
  scroll-behavior: smooth;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Muli", sans-serif;
}


.navbar-brand{
  font-size: 1.9rem;
  color: white!important;
}
.nav-link{
  color: white!important;
}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.navbar a:hover{
  transition: ease-in-out 0.3s;
  color: #3498db!important;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

/* Button css */
.btn-outline-primary{
  border-radius:20px;
  transition: 0.5s;
  letter-spacing: 1px;
  color: white;
}

.makingBold h1{
  font-weight: bold;
  color: white;
}
.header-img{
  text-align: center;
}
.brand-name{
  color: #3498db;
}
.des-color{
  color: #F0FFFF;
}
.overlay-bg{
  top:0;
  left:0;
  width: 100%;
  height: 100vh;
  z-index:2;
  background-color: rgba(0, 0, 0, 0.6);
}
#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
  width: 60%;
}

.home-page{
    background: url('https://images.unsplash.com/photo-1497215842964-222b430dc094?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1050&q=80');
    background-position:center;
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
}
.about-page{
  background: url('https://images.unsplash.com/photo-1497215728101-856f4ea42174?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=1050&q=80');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.top-text{
  font-weight: bold;
}


/* GalleryFilter starts */
.gallery-title
{
    font-size: 36px;
    color: black;
    text-align: center;
    font-weight: 500;
    margin-bottom: 70px;
}
.gallery-title:after {
    content: "";
    position: absolute;
    width: 40.5%;
    left: 30.5%;
    height: 45px;
    border-bottom: 1px solid #5e5e5e;
}
.filter-button
{
    font-size: 18px;
    border-radius: 5px;
    text-align: center;
    margin-right: 1.1rem;
    margin-bottom: 30px;

}
.gallery_product
{
    margin-bottom: 30px;
    cursor: pointer;
}
.card{
  border:none!important;
}
/* GalleryFilter ends */

@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
};




.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn-floating{
	height:40px;
	right:40px;
	border-radius:100px!important;
}
.btn-floating img{
  width: 20px;
}
